.start {
  animation: reduce-size 5s forwards ease-out;
  display: flex;
  justify-content: center;
  align-content: center;
  margin-top: 30vh;
}
@keyframes reduce-size {
  0% {
    opacity: 0;
    transform: scale(1.5);
  }

  100% {
    opacity: 1;
    transform: scale(0.5);
  }
}
